import React, { useEffect, useRef } from "react";
import dotspattern from "../../assets/images/dots-pattern.png";
import {
  handleMouseClick,
  handleMouseEnter,
  useIsInViewport,
} from "../../helpers/helper";
import TypeWriterEffect from "../TypeWriterEffect/TypeWriterEffect";
import DotsAnimation from "../DotsAnimation/DotsAnimation";

function CTA({ setActiveLink, activeLink }) {
  const viewportRef = useRef(null);
  const isInViewport = useIsInViewport(viewportRef);

  useEffect(() => {
    if (isInViewport) {
      setActiveLink({
        ...activeLink,
        banner: false,
        intro: false,
        lore: false,
        features: false,
        gravitars: false,
        roadmap: false,
        cta: true,
      });
    }
  }, [isInViewport]);

  return (
    <div className="feature-sec feature-even-sec cta-sec">
      <div ref={viewportRef} className="container">
        <div className="cta-sec-box">
          <div className="feature-heading-sec">
            <TypeWriterEffect
              className="cta-heading-sec"
              text="Explore the <span>Infinite </span><span class='pink'>expanses</span> of the galaxy"
            />
          </div>
          <a
            href="https://www.google.com/"
            target="_blank"
            className="common-btn wow animate__animated animate__fadeIn"
            onMouseEnter={handleMouseEnter}
            onClick={handleMouseClick}
          >
            <span>JOIN GRAVITALE</span>
          </a>
        </div>
        <h1 className="placeholder-text wow animate__animated animate__fadeIn">
          JOIN US
        </h1>
      </div>

      {/* <img src={dotspattern} className="dots-pattern" alt="" /> */}
      {/* <DotsAnimation /> */}
    </div>
  );
}

export default CTA;
